<template>
    <p-card>
        <template #content>
            <div class="p-fluid p-formgrid p-grid p-input-filled">
                <div class="p-field p-col-6 p-md-1">
                    <label class="p-mb-1">-</label>
                    <p-button
                        label="Assign Cycle"
                        @click="showAssignCycleDialog"
                        class="p-button-sm"
                    />
                </div>
                <div class="p-field p-col-12 p-md-1">
                    <label class="p-mb-1">From Date</label>
                    <p-calendar
                        :showIcon="true"
                        v-model="form.fromDate"
                        dateFormat="mm/dd/yy"
                        :class="{
                            'p-invalid': v$.fromDate.$invalid
                        }"
                    />
                    <i
                        v-if="v$.fromDate.validDate.$invalid"
                        class="pi pi-info-circle p-mt-2"
                        v-tooltip.right="v$.fromDate.validDate.$message"
                        style="color: #f44336; margin-left: 5px;"
                    ></i>
                </div>
                <div class="p-field p-col-12 p-md-1">
                    <label class="p-mb-1">To Date</label>
                    <p-calendar
                        :showIcon="true"
                        v-model="form.toDate"
                        dateFormat="mm/dd/yy"
                        :class="{
                            'p-invalid': v$.toDate.$invalid
                        }"
                    />
                    <i
                        v-if="v$.toDate.validDate.$invalid"
                        class="pi pi-info-circle p-mt-2"
                        v-tooltip.right="v$.toDate.validDate.$message"
                        style="color: #f44336; margin-left: 5px;"
                    ></i>
                </div>
                <div class="p-field p-col-6 p-md-1">
                    <label class="p-mb-1">-</label>
                    <p-button
                        label="Get Report"
                        @click="getOrders"
                        class="p-button-sm"
                    />
                </div>

                <div class="p-field p-col-6 p-md-2">
                    <label class="p-mb-1">-</label>
                    <p-button
                        label="ASSIGN CYCLE ALL"
                        @click="assignCycleONUnassignedTests"
                        class="p-button-sm app-button"
                    />
                </div>

                <div class="p-field p-col-6 p-md-2">
                    <label class="p-mb-1">Doctors</label>
                    <p-multi-select
                        v-model="form.selectedDoctors"
                        :options="doctors"
                        :filter="true"
                        @change="doctorsOnChange"
                        optionLabel="name"
                        optionValue="id"
                    />
                </div>
                <div class="p-field p-col-6 p-md-2">
                    <label class="p-mb-1">Location</label>
                    <p-multi-select
                        v-model="form.selectedLocations"
                        :options="selectedLocationOpts"
                        @change="doctorsOnChange"
                        optionLabel="name"
                    />
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { useDailryReportFormInit } from "@/composable/daily-report";
import { onMounted, onUnmounted, watch } from "vue";
import { emitter } from "@/event-bus";

export default {
    setup() {
        const {
            store,
            form,
            v$,
            submitted,
            getOrders,
            assignCycleONUnassignedTests,
            doctors,
            setDoctorSelected,
            selectedDoctor,
            selectedLocationOpts,
            showAssignCycleDialog,
            showCycleMonitoring,
            cycleMonitoringBtnLoading
        } = useDailryReportFormInit();

        const doctorsOnChange = (val) => {
            //console.log(val);
            store.commit("dailyReport/SET_SELECTED_REPORT", null);
        };

        onMounted(() => {
            store.dispatch("doctor/getDoctorList").then((_) => {
                setDoctorSelected();
            });
            store.dispatch("provider/getProviderLocations");

            emitter.on(
                "daily-report-report:assign-cycle",
                showAssignCycleDialog
            );
            emitter.on(
                "daily-report-report:view-cycle-monitoring",
                showCycleMonitoring
            );
        });

        onUnmounted(() => {
            emitter.off(
                "daily-report-report:assign-cycle",
                showAssignCycleDialog
            );
        });

        return {
            form,
            v$,
            submitted,
            getOrders,
            assignCycleONUnassignedTests,
            doctors,
            selectedDoctor,
            selectedLocationOpts,
            showAssignCycleDialog,
            showCycleMonitoring,
            cycleMonitoringBtnLoading,
            doctorsOnChange
        };
    }
};
</script>

<style scoped>
:deep() .p-calendar {
    height: 25px;
}
</style>
