<template>
    <p-data-table
        :value="fundedBillingList"
        class="p-mt-2 p-datatable-sm"
        :scrollable="true"
        scrollHeight="calc(100vh - 300px)"
        style="min-height: 500px"
        :loading="dataLoading"
        :lazy="true"
        v-model:selection="selectedList"
        dataKey="id"
    >
        <p-column
            headerStyle="width: 3em; max-width: 3rem"
            style="width: 3em; max-width: 3rem"
        >
            <template #header>
                <p-checkbox
                    v-model="selectAll"
                    :binary="true"
                    @change="selectAllOnChange"
                />
            </template>

            <template #body="{data}">
                <p-checkbox
                    v-model="checkList"
                    :name="data.id"
                    :value="data.id"
                    :disabled="data.man_review === '1'"
                />
            </template>
        </p-column>
        <p-column
            header="Demographic NO"
            field="demographic_no"
            headerStyle="width: 120px; max-width: 120px"
            style="width: 120px; max-width: 120px"
        />
        <p-column header="Name" field="demographic_name" />
        <p-column
            header="Cycle Type"
            field="cycle_type"
            headerStyle="width: 100px; max-width: 100px"
            style="width: 100px; max-width: 100px"
        />
        <p-column
            header="Header ID"
            field="bh_id"
            headerStyle="width: 100px; max-width: 100px"
            style="width: 100px; max-width: 100px"
        />
        <p-column
            header="Transaction ID"
            field="transc_id"
            headerStyle="width: 100px; max-width: 100px"
            style="width: 100px; max-width: 100px"
        />
        <p-column
            header="Date"
            field="billing_date"
            headerStyle="width: 100px; max-width: 100px"
            style="width: 100px; max-width: 100px"
        />
        <p-column
            header="Time"
            field="billing_time"
            headerStyle="width: 100px; max-width: 100px"
            style="width: 100px; max-width: 100px"
        />
        <p-column
            header="Total"
            field="total"
            headerStyle="width: 100px; max-width: 100px"
            style="width: 100px; max-width: 100px"
        />
        <p-column
            header="Push to Oscar"
            headerStyle="width: 100px; max-width: 100px"
            style="width: 100px; max-width: 100px"
        >
            <template #body="{data}">
                <span v-if="data.man_review === '-1'">Pushing...</span>
                <span v-else-if="data.man_review === '1'">Pushed</span>
                <p-button label="Push" v-else @click="pushToOscar(data)" />
            </template>
        </p-column>

        <template #footer>
            <p-button
                label="Push Selected Item to Oscar"
                class="p-mr-2"
                @click="pushAllToOscar"
                :loading="pushLoading"
            />
            <p-button
                label="Close"
                class="p-button-danger p-mr-2"
                @click="close"
            />
        </template>
    </p-data-table>
</template>

<script>
import { computed, inject, ref, watch } from "vue";
import { useStore } from "vuex";
import { emitter } from "@/event-bus";
export default {
    setup() {
        const store = useStore();

        const fundedBillingDialog = inject("fundedBillingDialog");
        const bloodBillingDialog = inject("bloodBillingDialog");

        const fundedBillingList = computed(
            () => store.state.dailyReport.billingList
        );
        const checkList = ref([]);
        const selectedList = computed(() => {
            return store.state.dailyReport.billingList.filter((item) =>
                checkList.value.includes(item.id)
            );
        });
        const dataLoading = inject("dataLoading");

        const selectAll = ref(false);
        const selectAllOnChange = () => {
            if (selectAll.value) {
                checkList.value = fundedBillingList.value
                    .filter((item) => item.man_review !== "1")
                    .map((item) => item.id);
            } else {
                checkList.value = [];
            }
        };

        const pushLoading = ref(false);
        const pushAllToOscar = () => {
            pushLoading.value = true;

            markSelectedReviewed("-1");

            store
                .dispatch("dailyReport/pushAllToOscar", {
                    ids: checkList.value,
                    funded: fundedBillingDialog.value
                })
                .then(() => {
                    // emitter.emit("billig-list-table:push-to-oscar");
                    markSelectedReviewed("1");
                })
                .finally(() => {
                    pushLoading.value = false;
                });
        };

        const pushToOscar = (data) => {
            data.man_review = "-1";

            // console.log("dailyReport/pushToOscar");
            store
                .dispatch("dailyReport/pushToOscar", {
                    id: data.id,
                    funded: fundedBillingDialog.value
                })
                .then(() => {
                    data.man_review = "1";
                });
        };

        const markSelectedReviewed = (review) => {
            const updatedList = fundedBillingList.value.map((item) => {
                if (checkList.value.includes(item.id)) {
                    return { ...item, man_review: review };
                }
                return item;
            });

            // Commit to Vuex
            store.commit("dailyReport/SET_BILLING_LIST", updatedList);
        };

        return {
            fundedBillingList,
            selectedList,
            dataLoading,
            pushToOscar,
            pushAllToOscar,
            markSelectedReviewed,
            pushLoading,
            checkList,
            selectAll,
            selectAllOnChange,
            close: () => {
                fundedBillingDialog.value = false;
                bloodBillingDialog.value = false;
            }
        };
    }
};
</script>
