<template>
    <p-dialog
        header="Blood Billing"
        v-model:visible="bloodBillingDialog"
        :modal="true"
        style="width: 70%"
        class="p-inputtext-sm p-input-filled"
    >
        <div class="p-formgroup-inline">
            <div class="p-field">
                <label>From Date</label>
                <p-calendar
                    v-model="form.fromDate"
                    :showIcon="true"
                    dateFormat="yy-mm-dd"
                    :class="{
                        'p-invalid': submitted && v$.fromDate.$invalid
                    }"
                />
            </div>
            <div class="p-field">
                <label>To Date</label>
                <p-calendar
                    v-model="form.toDate"
                    :showIcon="true"
                    dateFormat="yy-mm-dd"
                    :class="{
                        'p-invalid': submitted && v$.toDate.$invalid
                    }"
                />
            </div>
            <p-button
                label="Get Billing Data"
                class="p-mr-2"
                :loading="dataLoading"
                @click="getBloodBillingList"
            />
            <p-button label="Reset" />
        </div>

        <funded-billing-list-table />
    </p-dialog>
</template>

<script>
import { bloddBillingDialogInit } from "@/composable/daily-report";

import FundedBillingListTable from "@/components/daily-report/billing-list-table.vue";
import { provide } from "vue";

export default {
    components: {
        FundedBillingListTable
    },
    setup() {
        const {
            bloodBillingDialog,
            form,
            getBloodBillingList,
            dataLoading,
            v$,
            submitted
        } = bloddBillingDialogInit();

        provide("dataLoading", dataLoading);

        return {
            form,
            bloodBillingDialog,
            getBloodBillingList,
            dataLoading,
            v$,
            submitted,
            close: () => {
                bloodBillingDialog.value = false;
            }
        };
    }
};
</script>

<style scoped>
.p-calendar {
    height: 24.5px;
}
:deep() .p-datatable-tbody {
    min-height: 450px;
}
</style>
