<template>
    <p-dialog
        header="Funded Billing"
        v-model:visible="fundedBillingDialog"
        :modal="true"
        style="width: 70%"
        class="p-inputtext-sm p-input-filled"
    >
        <div class="p-formgroup-inline">
            <div class="p-field">
                <label>From Date</label>
                <p-calendar
                    v-model="form.fromDate"
                    :showIcon="true"
                    dateFormat="yy-mm-dd"
                />
            </div>
            <div class="p-field">
                <label>To Date</label>
                <p-calendar
                    v-model="form.toDate"
                    :showIcon="true"
                    dateFormat="yy-mm-dd"
                />
            </div>
            <div class="p-field">
                <label>Patient ID</label>
                <p-input-text v-model="form.patientId" />
            </div>
            <div class="p-field">
                <label>Cycle ID</label>
                <p-input-text v-model="form.cycleId" />
            </div>
            <div class="p-field">
                <label for="cycle-type" class="p-mb-1">
                    Cycle Type
                </label>
                <p-multi-select
                    v-model="form.cycleType"
                    style="width: 200px;"
                    :options="cycleTypesOptions"
                    optionLabel="label"
                    optionValue="label"
                />
            </div>
            <p-button
                label="Get Funded Billing Data"
                class="p-mr-2"
                :loading="dataLoading"
                @click="getFundedBloodBillingList"
            />
            <p-button label="Reset" />
        </div>

        <funded-billing-list-table />
    </p-dialog>
</template>

<script>
import { fundedBillingDialogInit } from "@/composable/daily-report";

import FundedBillingListTable from "@/components/daily-report/billing-list-table.vue";
import { onMounted, onUnmounted, provide } from "vue-demi";
import { emitter } from "@/event-bus";
import { ref } from "vue";

export default {
    components: {
        FundedBillingListTable
    },
    setup() {
        const {
            fundedBillingDialog,
            form,
            getFundedBloodBillingList,
            dataLoading
        } = fundedBillingDialogInit();

        // const selectedCycleType = ref(null);

        const cycleTypesOptions = ref([
            { label: "FET" },
            { label: "IVF" },
            { label: "Egg Donor" },
            { label: "Investigative" },
            { label: "ICSI" },
            { label: "IUI" },
            { label: "IC" },
            { label: "From Outside" },
            { label: "Recipient" },
            { label: "Satellite" },
            { label: "Cancelled" },
            { label: "Egg Freezing" }
        ]);

        provide("dataLoading", dataLoading);

        onMounted(() => {
            emitter.on(
                "billig-list-table:push-to-oscar",
                getFundedBloodBillingList
            );
        });

        onUnmounted(() => {
            emitter.off(
                "billig-list-table:push-to-oscar",
                getFundedBloodBillingList
            );
        });

        return {
            cycleTypesOptions,
            form,
            fundedBillingDialog,
            getFundedBloodBillingList,
            dataLoading,
            close: () => {
                fundedBillingDialog.value = false;
            }
        };
    }
};
</script>

<style scoped>
.p-calendar {
    height: 24.5px;
}
:deep() .p-datatable-tbody {
    min-height: 450px;
}
</style>
