<template>
    <p-dialog
        header="Assign Cycle"
        v-model:visible="assignCycleDialog"
        :modal="true"
        :style="{ width: '600px' }"
        @show="getCycleList"
    >
        <p-data-table
            :value="cycleList"
            v-model:selection="selectedCycle"
            selectionMode="single"
            dataKey="cycleId"
            :loading="cycleListLoading"
            class="p-datatable-sm p-mb-3"
        >
            <p-column header="ID" field="cycleId" />
            <p-column header="Date" field="cycleDate" />
            <p-column header="Type" field="cycleType" />
        </p-data-table>

        <p-button
            label="Assign"
            :disabled="!isSelected"
            @click="assignCycle"
            :loading="assignCycleLoading"
            class="p-mr-2 p-button-success"
        />
        <p-button
            label="Cancel"
            class="p-button-danger p-button-outlined"
            @click="close"
        />
    </p-dialog>
</template>

<script>
import { assignCycleDialogInit } from "@/composable/daily-report";
export default {
    setup() {
        const {
            assignCycleDialog,
            cycleList,
            cycleListLoading,
            getCycleList,
            selectedCycle,
            isSelected,
            assignCycle,
            assignCycleLoading
        } = assignCycleDialogInit();

        return {
            assignCycleDialog,
            cycleList,
            cycleListLoading,
            getCycleList,
            selectedCycle,
            isSelected,
            assignCycle,
            assignCycleLoading,
            close: () => {
                assignCycleDialog.value = false;
            }
        };
    }
};
</script>
