<template>
    <p-data-table
        :value="reportList"
        v-model:selection="selectedReport"
        selectionMode="single"
        dataKey="autoTestOrderId"
        responsiveLayout="scroll"
        showGridlines
        class="p-datatable-sm p-mt-1 p-shadow-1"
        :loading="reportLoading"
        :scrollable="true"
        scrollHeight="calc(100vh - 210px)"
        @row-dblclick="viewCycleMonitoring"
    >
        <p-column
            field="checked"
            style="min-width: 40px; max-width: 40px"
            headerStyle="min-width: 40px; max-width: 40px"
        >
            <template #body="{data}">
                <p-checkbox
                    v-model="data.checked"
                    :binary="true"
                    @change="checkedOneOnChange(data)"
                />
            </template>
        </p-column>
        <p-column
            header="#"
            field="testOrderId"
            style="min-width: 100px; max-width: 100px"
            headerStyle="min-width: 100px; max-width: 100px"
        />
        <p-column
            header="Chart #"
            field="patientId"
            style="min-width: 70px; max-width: 70px"
            headerStyle="min-width: 70px; max-width: 70px"
        />
        <p-column
            header="Last Name"
            field="lastName"
            style="min-width: 200px;"
            headerStyle="min-width: 200px;"
        />
        <p-column
            header="First Name"
            field="firstName"
            style="min-width: 200px;"
            headerStyle="min-width: 200px;"
        />
        <p-column
            header="Birth Date"
            field="birthDate"
            style="width: 80px; max-width: 80px"
            headerStyle="min-width: 80px; max-width: 80px"
        />

        <p-column
            header="Cycle"
            field="cycleType"
            style="min-width: 90px; max-width: 90px"
            headerStyle="min-width: 90px; max-width: 90px"
            :sortable="true"
        >
            <template #body="{data}">
                <span v-if="data.cycleType2 === 'No cycle'" style="color: red;">
                    {{ data.cycleType2 }}
                </span>
                <span v-else>{{ data.cycleType }}</span>
            </template>
        </p-column>

        <p-column
            header="Funded"
            field="fundedCycle"
            style="min-width: 40px; max-width: 40px"
            headerStyle="min-width: 40px; max-width: 40px"
        >
            <template #body="{data}">
                <p-checkbox
                    v-model="data.fundedCycle"
                    :binary="true"
                    class="custom-red-checkbox"
                />
            </template>
        </p-column>

        <p-column
            header="Date"
            field="orderDate"
            style="min-width: 80px; max-width: 80px"
            headerStyle="min-width: 80px; max-width: 80px"
        />
        <p-column
            header="E2"
            field="etwo"
            style="min-width: 60px; max-width: 60px"
            headerStyle="min-width: 60px; max-width: 60px"
            v-if="isDailyReport"
        >
            <template #body="{data}">
                <span
                    :style="isStyleRed('E2', data.etwo) ? { color: 'red' } : {}"
                    :class="{ number: !isNaN(data.etwo) }"
                >
                    <span
                        v-if="Number(data.etwo) === 11010"
                        style="background-color: red; color: white; padding: 2px"
                    >
                        Dilution
                    </span>
                    <span v-else>{{ data.etwo }}</span>
                </span>
            </template>
        </p-column>

        <p-column
            header="LH"
            field="lh"
            style="min-width: 60px; max-width: 60px"
            headerStyle="min-width: 60px; max-width: 60px"
            v-if="isDailyReport"
        >
            <template #body="{data}">
                <span
                    :style="isStyleRed('LH', data.lh) ? { color: 'red' } : {}"
                    :class="{ number: !isNaN(data.lh) }"
                    >{{ data.lh }}
                </span>
            </template>
        </p-column>

        <p-column
            header="PROG"
            field="prog"
            style="min-width: 60px; max-width: 60px"
            headerStyle="min-width: 60px; max-width: 60px"
        >
            <template #body="{data}">
                <span
                    :style="
                        isStyleRed('PROG', data.prog) ? { color: 'red' } : {}
                    "
                    :class="{ number: !isNaN(data.prog) }"
                    >{{ data.prog }}
                </span>
            </template>
        </p-column>

        <p-column
            header="FSH"
            field="fsh"
            style="min-width: 60px; max-width: 60px"
            headerStyle="min-width: 60px; max-width: 60px"
            v-if="isDailyReport"
        >
            <template #body="{data}">
                <span
                    :style="isStyleRed('FSH', data.fsh) ? { color: 'red' } : {}"
                    :class="{ number: !isNaN(data.fsh) }"
                    >{{ data.fsh }}
                </span>
            </template>
        </p-column>

        <p-column
            header="TSH"
            field="tsh"
            style="min-width: 60px; max-width: 60px"
            headerStyle="min-width: 60px; max-width: 60px"
            v-if="isDailyReport"
        >
            <template #body="{data}">
                <span
                    :style="isStyleRed('TSH', data.tsh) ? { color: 'red' } : {}"
                    :class="{ number: !isNaN(data.tsh) }"
                    >{{ data.tsh }}
                </span>
            </template>
        </p-column>

        <p-column
            header="PRL"
            field="prl"
            style="min-width: 60px; max-width: 60px"
            headerStyle="min-width: 60px; max-width: 60px"
            v-if="isDailyReport"
        >
            <template #body="{data}">
                <span
                    :style="isStyleRed('PRL', data.prl) ? { color: 'red' } : {}"
                    :class="{ number: !isNaN(data.prl) }"
                    >{{ data.prl }}
                </span>
            </template>
        </p-column>

        <p-column
            header="BhCG"
            field="bhcg"
            style="min-width: 60px; max-width: 60px"
            headerStyle="min-width: 60px; max-width: 60px"
        >
            <template #body="{data}">
                <span
                    :style="
                        isStyleRed('BHCG', data.bhcg) ? { color: 'red' } : {}
                    "
                    :class="{ number: !isNaN(data.bhcg) }"
                    >{{ data.bhcg }}
                </span>
            </template>
        </p-column>

        <p-column
            header="DHEAS"
            field="dheas"
            style="min-width: 60px; max-width: 60px"
            headerStyle="min-width: 60px; max-width: 60px"
            v-if="isDailyReport"
        >
            <template #body="{data}">
                <span
                    :style="
                        isStyleRed('DHEAS', data.dheas) ? { color: 'red' } : {}
                    "
                    :class="{ number: !isNaN(data.dheas) }"
                    >{{ data.dheas }}
                </span>
            </template>
        </p-column>

        <p-column
            header="AMHP"
            field="amh"
            style="min-width: 60px; max-width: 60px"
            headerStyle="min-width: 60px; max-width: 60px"
            v-if="isDailyReport"
        >
        </p-column>

        <p-column field="checked2" style="max-width: 3rem">
            <template #header>
                <p-checkbox :binary="true" @click="openUncheckAllDialoge" />
            </template>
            <template #body="{data}">
                <p-checkbox
                    v-if="data.checked2"
                    v-model="data.checked2"
                    :binary="true"
                    @change="checkedTowOnChange(data)"
                />

                <p-checkbox
                    v-if="!data.checked2"
                    v-model="data.checked2"
                    :binary="true"
                    @change="checkedTowOnChange(data)"
                    :disabled="!isDoctor"
                />
            </template>

            <!-- <template #body="{data}">
                <p-checkbox
                    v-model="data.checked2"
                    :binary="true"
                    @change="checkedTowOnChange(data)"
                    :disabled="!isDoctor"
                />
            </template> -->
        </p-column>
        <p-column
            header="Doctor"
            field="doctorName"
            :sortable="true"
            style="width: 100px; min-width: 120px"
        />
        <p-column
            header="Loc"
            field="location"
            :sortable="true"
            style="width: 100px; min-width: 120px"
        />
        <p-column
            header="Modified"
            field="lastModifiedUser"
            style="width: 120px; min-width: 120px"
        />
        <p-column
            header="Modified !!"
            field="lastModifiedUser2"
            style="width: 120px; min-width: 120px"
        />

        <template #footer>
            <div class="p-d-flex p-jc-between footer-buttons">
                <div>
                    <p-button
                        label="Assign Cycle"
                        class="p-mr-2"
                        @click="assignCycle"
                    />

                    <p-button
                        label="Deassign Cycle"
                        class="p-mr-2"
                        @click="deassignCycle"
                    />

                    <p-button
                        label="Encounter"
                        class="p-mr-2"
                        @click="openEncounter"
                    />

                    <p-button
                        label="View Blood Billing"
                        class="p-mr-2"
                        @click="showBloodBillingDialog"
                    />

                    <p-button
                        label="View Funded Billing"
                        class="p-mr-2"
                        @click="showFundedBillingDialog"
                    />

                    <p-button
                        label="View Cycle Monitoring"
                        class="p-mr-2"
                        @click="viewCycleMonitoring"
                        :loading="cycleMonitoringBtnLoading"
                    />
                    <p-button
                        label="PDF Blood Report"
                        class="p-mr-2"
                        @click="openPdfReport"
                    />
                </div>
                <div class="p-d-flex p-ai-center p-mr-2">
                    {{ isDoctor }} Total : {{ reportList.length }}
                </div>
            </div>
        </template>
    </p-data-table>
    <p-dialog
        v-model:visible="uncheckAllDoctor"
        modal
        header="Uncheck All !!"
        :style="{ width: '30rem' }"
    >
        <p>
            Are you sure to uncheck all of yours?
        </p>
        <p-button label="No" class="p-mr-2" @click="cancel" />
        <p-button
            label="Yes"
            class="p-button-danger"
            @click="uncheckAll"
            :loading="uncheckAllLoading"
        />
    </p-dialog>
</template>

<script>
import { useDailyReportReportsInit } from "@/composable/daily-report";
import { emitter } from "@/event-bus";
import { computed } from "vue-demi";
import { $axios } from "@/service/app-http";
import { inject, ref } from "vue";
import { useToast } from "primevue/usetoast";

export default {
    setup() {
        const toast = useToast();
        const {
            store,
            reportList,
            reportListChecked2ByDoctor,
            selectedReport,
            reportLoading,
            checkedOneOnChange,
            checkedTowOnChange,
            cycleMonitoringBtnLoading,
            fundedBillingDialog,
            bloodBillingDialog,
            openPdfReport,
            isStyleRed,
            openEncounter,
            deassignCycle,
            isDoctor,
            currentProviderNo
        } = useDailyReportReportsInit();

        const uncheckAllDoctor = ref(false);
        const uncheckAllLoading = ref(false);

        const cycleMonitoringDialog = inject("cycleMonitoringDialog");

        const cancel = () => {
            uncheckAllDoctor.value = false;
        };

        const uncheckAll = () => {
            let todolist = [];

            reportListChecked2ByDoctor.value.forEach((element) => {
                todolist.push(element.autoTestOrderId);
            });

            const payload = {
                testOrderIds: todolist
            };

            if (todolist.length > 0) {
                uncheckAllLoading.value = true;
                $axios.post(`daily-report/uncheckall`, payload).then(() => {
                    reportListChecked2ByDoctor.value.forEach((element) => {
                        element.checked2 = false;
                    });

                    toast.add({
                        severity: "success",
                        summary: "Success",
                        detail: "Update Successfully",
                        life: 3000
                    });

                    uncheckAllLoading.value = false;
                    uncheckAllDoctor.value = false;
                });
            }
        };

        const openUncheckAllDialoge = () => {
            let todolist = [];

            reportListChecked2ByDoctor.value.forEach((element) => {
                todolist.push(element.autoTestOrderId);
            });

            if (todolist.length > 0) {
                uncheckAllDoctor.value = true;
            }
        };

        const isDailyReport = computed(
            () => store.state.dailyReport.isDailyReport
        );

        return {
            uncheckAllDoctor,
            uncheckAllLoading,
            uncheckAll,
            cancel,
            openUncheckAllDialoge,
            currentProviderNo,
            reportList,
            reportLoading,
            selectedReport,
            checkedOneOnChange,
            checkedTowOnChange,
            cycleMonitoringBtnLoading,
            fundedBillingDialog,
            cycleMonitoringDialog,
            showFundedBillingDialog: () => {
                fundedBillingDialog.value = true;
            },
            showCycleMonitoringDialog: () => {
                cycleMonitoringDialog.value = true;
            },
            showBloodBillingDialog: () => {
                bloodBillingDialog.value = true;
            },
            assignCycle: () => {
                emitter.emit("daily-report-report:assign-cycle");
            },
            deassignCycle,
            viewCycleMonitoring: () => {
                emitter.emit("daily-report-report:view-cycle-monitoring");
            },
            openPdfReport,
            isStyleRed,
            openEncounter,
            isDailyReport,
            isDoctor
        };
    }
};
</script>

<style scoped>
.number {
    font-size: 14px;
    font-weight: bold;
}

:deep() .custom-red-checkbox .p-checkbox-box.p-highlight {
    background-color: red !important;
    border-color: red !important;
    color: white !important;
}

:deep() .custom-red-checkbox .p-checkbox-icon {
    color: white !important;
}

@media (max-width: 768px) {
    .footer-buttons {
        flex-direction: column;
        width: 100%;
    }

    .footer-buttons .p-button {
        width: 100%;
        margin-bottom: 0.3rem;
    }
}
</style>
